/* Global style setting */

body {
    line-height: 1.5;
    overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: $font-family-sans-serif-title;
    font-weight: $font-weight-normal;
    margin-bottom: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto; 
    object-fit: cover;
}

a {
    color: inherit;
    
    &:hover{
        text-decoration: none;
    }
}

p {
    margin-bottom: 0;
}

ul, ol {
    list-style: none;
    margin-bottom: 0;
}

ul {
    padding-left: 0;
}

label {
    margin-bottom: 0;
}

sup {
    vertical-align: super;
}

.material-icons {
    vertical-align: bottom;
}